.container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px 30px;
}
.label{
font-weight: bold;
  flex: 1;
  color:white;
  margin-bottom: 0.5rem;
}
.contact-form {
    margin: 3rem auto;
    max-width: 25rem;
    border-radius: 6px;
    background-color: #38015c;
    box-shadow: 0 1px 4pxrgba(0,0,0,.2);
    padding: 1rem;
    text-align: center;
    color: #fff;
}
.input{
flex: 3;
  font: inherit;
  padding: 0.35rem 0.35rem;
  border-radius: 6px;
  border: 1px solid #ccc;
}
.form-style-6{
	font: 95% Arial, Helvetica, sans-serif;
	max-width: 400px;
	margin: 10px auto;
	padding: 16px;
	background: #F7F7F7;
}
.form-style-6 h1{
	background: blue;
	padding: 20px 0;
	font-size: 140%;
	font-weight: 300;
	text-align: center;
	color: #fff;
	margin: -16px -16px 16px -16px;
}
.form-style-6 button:hover{
	background: #2EBC99;
}
.form-style-6 input
{
	box-shadow: 0 0 5px #43D1AF;
	padding: 6%;
	border: 1px solid #43D1AF;
}